import React from 'react'
import { Divider, Radio, Table, Pagination } from 'antd'

const columns = [
  {
    title: 'Name',
    dataIndex: 'name',
    render: (text) => <a>{text}</a>
  },
  {
    title: 'Age',
    dataIndex: 'age'
  },
  {
    title: 'Address',
    dataIndex: 'address'
  }
]
const data = [
  {
    key: '1',
    name: 'John Brown',
    age: 32,
    address: 'New York No. 1 Lake Park'
  },
  {
    key: '2',
    name: 'Jim Green',
    age: 42,
    address: 'London No. 1 Lake Park'
  },
  {
    key: '3',
    name: 'Joe Black',
    age: 32,
    address: 'Sidney No. 1 Lake Park'
  },
  {
    key: '4',
    name: 'Disabled User',
    age: 99,
    address: 'Sidney No. 1 Lake Park'
  }
]
class ProductLists extends React.Component {
  constructor(props) {
    super(props)
    this.tableRef = React.createRef()
  }
  componentDidMount() {
    console.log(this.tableRef, 'ref')
  }
  render() {
    return (
      <div>
        <Table
          ref={this.tableRef}
          pagination={false}
          columns={columns}
          dataSource={data}
        />
        <Pagination
          total={85}
          showTotal={(total) => `Total ${total} items`}
          defaultPageSize={20}
          defaultCurrent={1}
        />
      </div>
    )
  }
}

export default ProductLists
