import React from 'react'
import { connect } from 'react-redux'
import {
  increaseHandle,
  minusHandle,
  addAfterMinutesHandle,
  minusAfterMinutesHandle
} from '@/store/actions/counterActions'
import { Button, Space } from 'antd'

class ReduxDemo extends React.Component {
  addHandle = () => {
    console.log('test')
    console.log(this.props, 'this.props')
    this.props.increaseHandle(1)
  }
  addHandle2 = () => {
    console.log('test')
    console.log(this.props, 'this.props')
    this.props.increaseHandle(2)
  }
  minusHandle = () => {
    console.log('test')
    console.log(this.props, 'this.props')
    this.props.minusHandle(1)
  }
  minusHandle2 = () => {
    console.log('test')
    console.log(this.props, 'this.props')
    this.props.minusHandle(2)
  }
  addAfterHandle = () => {
    this.props.addAfterHandle(1)
  }
  addAfterHandle2 = () => {
    this.props.addAfterHandle(2, 2000)
  }
  minusAfterHandle = () => {
    this.props.minusAfterHandle(1, 2000)
  }
  minusAfterHandle2 = () => {
    this.props.minusAfterHandle(2, 2000)
  }
  render() {
    console.log(this.props, 'props')
    return (
      <div>
        <h2>redux的交互</h2>
        <p>{this.props.count}</p>
        同步加减：
        <Space>
          <Button onClick={this.addHandle}>add</Button>
          <Button type='danger' onClick={this.minusHandle}>
            add - 1
          </Button>
          <Button type='primary' onClick={this.addHandle2}>
            add + 2
          </Button>
          <Button type='danger' onClick={this.minusHandle2}>
            add - 2
          </Button>
        </Space>
        <br />
        <br />
        异步加减：
        <Space>
          <Button onClick={this.addAfterHandle}> 一秒后add</Button>
          <Button type='danger' onClick={this.minusAfterHandle}>
            一秒后 add - 1
          </Button>
          <Button type='primary' onClick={this.addAfterHandle2}>
            两秒后add + 2
          </Button>
          <Button type='danger' onClick={this.minusAfterHandle2}>
            两秒后add - 2
          </Button>
        </Space>
      </div>
    )
  }
}
const delay = (num, ms = 1000) =>
  new Promise((resolve, reject) => setTimeout(() => resolve(num), ms))

const mapStateToProps = (state) => {
  console.log(state, 'state')
  return {
    count: state.counterReducer.count
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    increaseHandle: (num) => dispatch(increaseHandle({ num })),
    minusHandle: (num) => dispatch(minusHandle({ num })),
    addAfterHandle: async (count, ms) => {
      //模拟异步请求，主要是发接口请求
      let num = await delay(count, ms)
      console.log(num, 'res')
      dispatch(addAfterMinutesHandle({ num }))
    },
    minusAfterHandle: async (count, ms) => {
      //模拟异步请求，主要是发接口请求
      let num = await delay(count, ms)
      console.log(num, 'res')
      dispatch(minusAfterMinutesHandle({ num }))
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ReduxDemo)
