import {
  INCREASE_NUMBER,
  MINUS_NUMBER,
  ONEMINUTESADD,
  ONEMINUTESMINUS
} from '../constant'
export const increaseHandle = (payload) => {
  return {
    type: INCREASE_NUMBER,
    payload
  }
}

export const minusHandle = (payload) => {
  return {
    type: MINUS_NUMBER,
    payload
  }
}

export const addAfterMinutesHandle = (payload) => {
  return {
    type: ONEMINUTESADD,
    payload
  }
}
//minusAfterMinutesHandle
export const minusAfterMinutesHandle = (payload) => {
  return {
    type: ONEMINUTESMINUS,
    payload
  }
}
