import React, { useState } from 'react'
import {
  MenuFoldOutlined,
  MenuUnfoldOutlined,
  DownOutlined
} from '@ant-design/icons'
import {
  Routes,
  Route,
  Navigate,
  useNavigate,
  Outlet,
  useParams
} from 'react-router-dom'
import { Layout, Menu, theme, Avatar, Dropdown, Space, Typography } from 'antd'
import './layout.less'
import { siderBarRoutes } from '@/routes'
import ErrorPage from '@views/error/error404'
const { Header, Sider, Content } = Layout
const rootSubmenuKeys = ['/', '/product', '/complibs']
const LayoutMain = () => {
  const [collapsed, setCollapsed] = useState(false)
  // const [activeMenu, setActiveMenu] = useState('/home')
  const [openKeys, setOpenKeys] = useState(['/'])
  const navigator = useNavigate()
  const onClick = (e) => {
    console.log(e, 'useNavigate')
    navigator(e.key)
    // setActiveMenu(e.key)
  }
  const onOpenChange = (keys) => {
    console.log(keys, 'keys')
    const latestOpenKey = keys.find((key) => openKeys.indexOf(key) === -1)
    if (rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
      setOpenKeys(keys)
    } else {
      setOpenKeys(latestOpenKey ? [latestOpenKey] : [])
    }
  }
  const items = [
    {
      key: 'user',
      label: '用户中心'
    },
    {
      key: 'logout',
      label: '退出',
      danger: true,
      onClick: () => {
        localStorage.removeItem('token')
        navigator('/home')
      }
    }
  ]
  return (
    <Layout>
      <Sider trigger={null} collapsible collapsed={collapsed}>
        <h1 className='nav-title'>我的后台管理系统</h1>
        <Menu
          theme='dark'
          mode='inline'
          onClick={onClick}
          items={siderBarRoutes}
          defaultSelectedKeys={['/']}
          defaultOpenKeys={['/home']}
        />
      </Sider>
      <Layout className='site-layout'>
        <Header
          className='site-layout-header'
          style={{ padding: 0, background: '#fff' }}
        >
          <div>
            {React.createElement(
              collapsed ? MenuUnfoldOutlined : MenuFoldOutlined,
              {
                className: 'trigger',
                onClick: () => setCollapsed(!collapsed)
              }
            )}
          </div>
          <div className='right-info'>
            <Avatar size='large' src='https://joesch.moe/api/v1/random?key=1' />
            <Dropdown
              menu={{
                items
              }}
            >
              <Typography.Link>
                <Space>
                  admin，欢迎回来
                  <DownOutlined />
                </Space>
              </Typography.Link>
            </Dropdown>
          </div>
        </Header>
        <Content
          style={{
            margin: '24px 16px',
            padding: 24,
            minHeight: 280,
            background: '#fff'
          }}
        >
          {/* {activeMenu} --- */}
          {/* 渲染子路由 匹配到子路由时，用子路由的组件替换此处内容*/}
          {/* 类似Vue中的router-view */}
          <Outlet />
        </Content>
      </Layout>
    </Layout>
  )
}

export default LayoutMain
