import {
  INCREASE_NUMBER,
  MINUS_NUMBER,
  ONEMINUTESADD,
  ONEMINUTESMINUS
} from '../constant'

const initState = {
  count: 0
}

const counterReducer = (state = initState, action) => {
  switch (action.type) {
    case INCREASE_NUMBER:
      console.log(action.payload, 'payload')
      return { ...state, count: state.count + action.payload.num }
    case MINUS_NUMBER:
      console.log(action.payload, 'payload')
      return { ...state, count: state.count - action.payload.num }
    case ONEMINUTESADD:
      console.log(action.payload, 'ONEMINUTESADD')
      return { ...state, count: state.count + action.payload.num }
    case ONEMINUTESMINUS:
      console.log(action.payload, 'ONEMINUTESMINUS')
      return { ...state, count: state.count - action.payload.num }
    default:
      return state
  }
}

export default counterReducer
