import { createHashRouter } from 'react-router-dom'
import React from 'react'
import Analysis from '@views/Analysis/Analysis'
import Login from '@views/Login/Login'
import Dashboard from '@views/Dashboard/Dashboard'
import ErrorPage from '@views/error/error404'
import ProductLists from '@views/ProductLists/ProductLists'
import ProductDetails from '@views/ProductDetails/ProductDetails'
import DatePickerComp from '@views/CompLibs/DatePickerComp'
import LayoutMain from '@/layout'
import Author from '@views/AuthorRouter'
import ReactRedux from '@views/ReactRedux'
import {
  UserOutlined,
  VideoCameraOutlined,
  UnorderedListOutlined
} from '@ant-design/icons'

const filterRoutesWithNoIcon = (arr) => {
  return arr.filter((ele, i) => {
    if (ele.children && ele.children.length > 0) {
      ele.children = filterRoutesWithNoIcon(ele.children)
    }
    if (!ele.hidden) {
      delete ele.icon
      return ele
    }
  })
}

// 主路由
export const routesItems = [
  {
    key: '',
    path: '/',
    label: '首页',
    element: (
      <Author>
        <LayoutMain />
      </Author>
    ),
    // errorElement: <ErrorPage />,
    children: [
      {
        key: '/home',
        path: '/home',
        label: '数据看板',

        element: <Dashboard />
      },
      {
        key: '/system',
        path: '/system',
        label: '统计分析',
        element: <Analysis />
      }
    ]
  },
  {
    key: '/product',
    path: '/product',
    label: '产品管理',
    element: <LayoutMain />,
    // errorElement: <ErrorPage />,
    children: [
      {
        key: '/product/lists',
        path: '/product/lists',
        label: '产品列表',
        element: <ProductLists />
      },
      {
        key: '/product/detail',
        path: '/product/detail',
        label: '产品详情',
        element: <ProductDetails />
      }
    ]
  },
  {
    key: '/complibs',
    path: '/complibs',
    label: '组件管理',
    element: <LayoutMain />,
    // errorElement: <ErrorPage />,
    children: [
      {
        key: '/complibs/datePicker',
        path: '/complibs/datePicker',
        label: '日期组件',
        element: <DatePickerComp />
      }
    ]
  },
  {
    key: '/redux',
    path: '/redux',
    label: '数据共享',
    element: <LayoutMain />,
    children: [
      {
        key: '/redux/react-redux',
        path: '/redux/react-redux',
        label: 'react-redux交互',
        element: <ReactRedux />
      }
    ]
  },
  {
    label: '登录',
    path: '/login',
    element: <Login />,
    noAuth: true,
    hidden: true
  },
  {
    path: '*',
    element: <ErrorPage />
  }
]
let routes = JSON.parse(JSON.stringify(routesItems))
const router = createHashRouter(routesItems)

const filterRoutes = (arr) => {
  return arr.filter((ele, i) => {
    if (ele.children && ele.children.length > 0) {
      console.log('children')
      ele.children = filterRoutes(ele.children)
    }
    if (!ele.hidden) {
      delete ele.hidden
      delete ele.noAuth
      delete ele.exact
      return ele
    }
  })
}

export const siderBarRoutes = filterRoutes(routes)

export default router
